import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import Pagination from '../Base/Paginations/Pagnations';
import {getAllLedRegister} from './../Apis.js'
//import ledRegisterData from './ledRegisterData'
const addledRegister = 'leds/add'
const addLed = '/led/add'
function LedRegisterRow(props) {
  const ledRegister = props.ledRegister
  const ledRegisterLink = `/leds/${ledRegister.id}`
  const updateledRegister = `led/update/${ledRegister.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={ledRegister.id.toString()}>
      <th scope="row"><Link to={ledRegisterLink}>{ledRegister.id}</Link></th>
      <td ><Link to={ledRegisterLink}>{ledRegister.name}</Link></td>
      {/* <td >{ledRegister.email}</td>
      <td>{ledRegister.status?'Enable':'Disable'}</td> */}
      {/* <td >{ledRegister.hospital_id}</td> */}
      <td ><Link to={updateledRegister}><span class="cui-pencil" aria-hidden="true"></span></Link></td>
    </tr>
  )
}

class LedRegister extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      totalPages: 0,
      totalRecords: 0
    };
  }


  componentDidMount() {
    this.getAllLedRegister(1)
  
  }
  getAllLedRegister=async(page)=>{
    const responseData= await getAllLedRegister(page);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
              isLoaded: true,
              items: responseData.data.result,
              totalPages: responseData.data.pages,
              totalRecords: responseData.data.count
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  onPageChanged = data => {
    //const { allCountries } = this.state;
   
    const { currentPage, totalPages, pageLimit } = data;
    this.getAllLedRegister(currentPage)
    console.log(data);
  };

  render() {

    const ledRegisterList = this.state.items; //ledRegisterData.filter((ledRegister) => ledRegister.id < 10)
    const Totrecords = parseInt(this.state.totalRecords); // total ledRegister records.
    let pagination;

    if(Totrecords !== 0 ){
      pagination = <Pagination totalRecords={Totrecords} pageLimit={5} pageNeighbours={1} onPageChanged={this.onPageChanged} />
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={2}>
          </Col>
          <Col xl={8}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Smart Screen 
                {/* <Link to={addledRegister}><button onClick={this.showHospitalInfo} className='btn btn-success registerBtn upActionbtn'>Register Smart Screen</button></Link> */}
                <Link to={addLed}><button onClick={this.showHospitalInfo} className='btn btn-success  upActionbtn'>+ Add Smart Screen</button></Link>

              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Id</th>
                      <th scope="col">Name</th>
                      {/* <th scope="col">Patient Email</th>
                      <th scope="col">Status</th> */}
                      {/* <th scope="col">Hospital Id</th> */}
                      <th scope="col">Edit Smart Screen</th>
                    </tr>
                  </thead>
                  <tbody>
                    {ledRegisterList.map((ledRegister, index) =>
                      <LedRegisterRow key={index} ledRegister={ledRegister}/>
                    )}
                  </tbody>
                </Table>
              </CardBody>              
              {pagination}
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default LedRegister;
